.conteiner {
    // background-color: burlywood;
    display: grid;
    padding: 5px;
    align-self: baseline;
    .conteiner__elem {
        background: linear-gradient(to bottom, rgba(39, 170, 225, .8), rgba(28, 117, 188, .8)), url('https://themified.com/friend-finder/images/covers/1.jpg')no-repeat;
        // background-image: url();
        background-size: cover;
        width: 100%;
        height: 115px;
        border-radius: 4px;
        padding: 10px 20px;
        color: #fff;
        margin-bottom: 40px;
        img {
            border: 7px solid #fff;
            float: left;
            margin-right: 20px;
            position: relative;
            top: -11px;
            right: 24px;
            height: 70px;
            width: 70px;
            border-radius: 50%;
        }
        color: white;
        font-family: inherit;
        font-weight: 500;
        line-height: 1.1;
    }
    .active {
        background: linear-gradient(to bottom, rgba(39, 170, 225, 0.8), rgba(28, 117, 188, 0.8)), url(https://themified.com/friend-finder/images/covers/1.jpg) no-repeat;
        background-size: cover;
        width: 100%;
        border-radius: 4px;
        color: #fff!important;
        color: white;
        font-family: inherit;
        font-weight: 500;
    }
}

.conteiner a {
    color: #6d6e71;
    padding: 5px;
    text-decoration: none;
    position: relative;
    &:hover {
        text-decoration: none;
    }
    &:after {
        content: "";
        width: 100%;
        height: 1px;
        border-top: 1px solid #f1f2f2;
        position: absolute;
        bottom: -0;
        left: 0;
    }
}

.active {
    color: red;
    background-color: red;
    background: linear-gradient(to bottom, rgba(39, 170, 225, 0.8), rgba(28, 117, 188, 0.8)), url(https://themified.com/friend-finder/images/covers/1.jpg) no-repeat;
    background-size: cover;
    width: 100%;
    border-radius: 4px;
    padding: 10px 20px;
    color: #fff!important;
    color: white;
    font-family: inherit;
    font-weight: 500;
}