.dialogs {
    padding: 20px;
    a {
        color: #0d6efd;
        text-decoration: none;
    }
    img {
        height: 40px;
        border-radius: 50%;
        margin-right: 10px;
    }
}

.dialogs .active {
    border-bottom: 1px solid #8dc63f;
}

.dialog {
    border-bottom: 1px solid #f1f2f2;
    cursor: pointer;
    position: relative;
    &:hover {
        border-bottom: 1px solid #8dc63f;
    }
    small {
        margin-top: -5px;
        position: absolute;
        content: '';
        right: 0;
        width: 70px;
        height: 100%;
    }
}

.dialog,
.message {
    padding: 5px;
    padding-top: 15px;
    margin-top: 5px;
}

.message {
    overflow-wrap: break-word;
    &:first-child {
        background-color: rgba(141, 198, 63, .1);
    }
    background: rgba(39, 170, 225, .1);
    padding: 5px 10px;
    position: relative;
    border-radius: 10px;
}

.dialogs__items,
.messages {
    position: relative;
    &::before {
        position: absolute;
        content: '';
        right: 0;
        width: 8px;
        height: 100%;
        border-radius: 5px;
        background-color: #eeeeee;
    }
}

.profilText {
    border-color: #0d6efd;
    border-radius: 5px;
    resize: none;
    margin-top: 5px;
}

.whoToFollow {
    cursor: pointer;
    border-radius: 5px;
    span {
        color: #0d6efd;
        text-decoration: none;
    }
    &:hover {
        background-color: #cadaf2;
    }
}

@media only screen and (max-width: 960px) {
    .ElemwhoToFollow {
        display: none;
        background-color: #8dc63f;
    }
    .dialogs__items,
    .messages {
        &::before {
            width: 0;
        }
    }
}