.post {
    color: brown;
}

.profil {
    border: 10px solid #fff;
    border-radius: 50%;
    height: 200px;
    width: 200px;
}

.name {
    font-family: 'Lato', sans-serif;
}

.profilImg {
    display: flex;
    flex-direction: column;
}

.form_control {
    width: 64%!important;
    color: #0d6efd!important;
    border-color: #0d6efd!important;
    &:hover {
        color: #fff!important;
        background-color: #0d6efd!important;
        border-color: #0d6efd!important;
    }
}

.write {
    cursor: pointer;
    &:hover {
        background-color: rgba(175, 199, 209, 0.8);
    }
}

.s.container_blok {
    content: "";
    width: 100%;
    height: 1px;
    border-top: 1px solid #f1f2f2;
    position: absolute;
    bottom: -0;
    left: 0;
}

.profilText {
    border-color: #0d6efd;
    border-radius: 5px;
    resize: none;
    margin-top: 5px;
}