.photo {
    width: 100px;
    height: 100px;
}

.curentPage {
    font-weight: bold;
}

.user {
    padding: 20px 0;
    border-top: 1px solid #f1f2f2;
    border-bottom: 1px solid #f1f2f2;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    a {
        display: flex;
        justify-content: space-between;
        text-decoration: none;
        color: #27aae1;
        font-weight: 600;
        div {
            margin-left: 20px;
        }
    }
}

.pagination {
    display: flex;
}