.footer {
    margin-top:50px;
    position: relative;
    &:before {
        content: "";
        background: linear-gradient(to right, #ffffff 0%, #53cbf1 50%, #ffffff 100%);
        height: 5px;
        width: 100%;
        position: absolute;
    }
    li {
        font-size: 14px;
        padding-bottom: 5px;
        cursor: pointer;
        list-style-type: circle;
        &:first-child {
            padding-bottom: 10px;
            color: #27aae1;
            cursor: cell;
            list-style-type: none;
        }
        &:hover {
            color: #27aae1;
        }
        &::marker {
            font-size: 150%;
            color: #27aae1;
        }
    }
}

.copyright {
    background: #231F20;
    text-align: center;
    color: #fff;
    padding: 12px 0 4px;
}
